import React, { Component } from 'react';
import { Card, CardBody, CardImg, CardText, 
    CardTitle, Breadcrumb, BreadcrumbItem, 
    Modal, ModalBody, ModalHeader, 
    Row, Col, Button, Label} from "reactstrap";
import { Link } from "react-router-dom";
import { LocalForm, Control, Errors } from "react-redux-form";
import { Loading } from './LoadingComponent';
import { baseUrl } from "../shared/baseUrl";
import { FadeTransform, Fade, Stagger } from "react-animation-components";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);

    class CommentForm extends Component {

        constructor(props) {
            super(props);
            this.state = {
                isModalOpen: false
            }
            this.toggleModal = this.toggleModal.bind(this);
        }

        toggleModal = () => {
            this.setState({
                isModalOpen: !this.state.isModalOpen
            });
        }

        handleSubmit = (values) => {
            this.toggleModal();
            this.props.postComment(this.props.dishId, values.rating, values.name, values.comments);
        }

        render() {
            return(
                <>
                    <Button outline onClick={this.toggleModal}>
                        <span className="fa fa-pencil fa-lg"> Submit comment</span>
                    </Button>
                    <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                        <ModalHeader toggle={this.toggleModal}>Submit comment</ModalHeader>
                        <ModalBody>
                            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                                <Row className="form-group">
                                    <Label md={2} htmlFor="rating">Rating</Label>
                                    <Col md={10}>
                                        <Control.select id="rating" name="rating" 
                                          model=".rating" className="form-control" >
                                            <option>1</option>
                                            <option>2</option>
                                            <option>3</option>
                                            <option>4</option>
                                            <option>5</option>
                                        </Control.select>
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label md={2} htmlFor="name">Your Name</Label>
                                    <Col md={10}>
                                        <Control.text id="name" name="name"
                                          model=".name" placeholder="Enter your name" 
                                          className="form-control" 
                                          validators = {{
                                            required, minLength: minLength(3), maxLength: maxLength(15) 
                                          }} />
                                          <Errors 
                                            show="touched" 
                                            className="text-danger" 
                                            model=".name"
                                            messages={{
                                                required: 'Required',
                                                minLength: 'Must be >=3 characters',
                                                maxLength: 'Must be <=15 characters'
                                            }} />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                                    <Label md={2} htmlFor="comments">Comments</Label>
                                    <Col md={10}>
                                        <Control.textarea name="comments" id="comments"
                                          model=".comments" className="form-control" rows="6" />
                                    </Col>
                                </Row>
                                <Row className="from-group offset-md-2">
                                    <Button type="submit" color="primary">Submit</Button>
                                </Row>
                            </LocalForm>
                        </ModalBody>
                    </Modal>
                </>
            );
        }
    }

    const RenderComments = ({ comments, postComment, dishId }) => {
        if (comments.length !== 0) {
            const cmnts = comments.map((cmt) => {
                return (
                    <Fade in>
                        <ul className="list-unstyled" key={cmt.id}>
                            <li> {cmt.comment} </li>
                            <li> --{cmt.author}, {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit' }).format(new Date(Date.parse(cmt.date)))} </li>
                        </ul>
                    </Fade>
                );
            });
            return (
                <div>
                    <h4>Comments</h4>
                    <ul className="list-unstyled">
                        <Stagger in>
                            {comments.map((cmt) => {
                                return (
                                    <Fade in>
                                        <li key={cmt.id}>
                                            <p>{cmt.comment}</p>
                                            <p>-- {cmt.author}</p>
                                        </li>
                                    </Fade>
                                );
                            })}
                        </Stagger>
                    </ul>
                    {/*cmnts*/} 
                    <CommentForm dishId={dishId} postComment={postComment}/>
                </div>
            );
        }
        else {
            return (
                <div></div>
            );
        }
    }

    const DishDetail = (props) => {
        if(props.isLoading) {
            return(
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            );
        }
        else if(props.errMess) {
            return(
                <div className="container">
                    <div className="row">
                        <h4>{props.errMess}</h4>
                    </div>
                </div>
            );
        }
        else if(props.dish != null) {
            return(
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                            <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                        </Breadcrumb>
                        <div className="col-12">
                            <h3>{props.dish.name}</h3>
                            <hr />
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12 col-md-5 m-1">
                            <FadeTransform in
                                transformProps={{
                                    exitTransform: 'sacle(0.5) translateY(-50%)'
                            }}>
                                <Card>
                                    <CardImg width="100%" src={baseUrl + props.dish.image} alt={props.dish.name} />
                                    <CardBody>
                                        <CardTitle>{props.dish.name}</CardTitle>
                                        <CardText>{props.dish.description}</CardText>
                                    </CardBody>    
                                </Card>
                            </FadeTransform>
                        </div>
                        <div className="col-12 col-md-5 m-1">
                            <RenderComments comments={props.comments} 
                                postComment={props.postComment}
                                dishId={props.dish.id}
                            />
                        </div>
                    </div>   
                </div>
            );
        }
    }


export default DishDetail;