import React , { Component } from 'react';
import { Navbar, NavbarBrand , Nav, NavbarToggler, 
  Collapse, NavItem, Jumbotron, Modal, ModalBody, ModalHeader, 
  Form, FormGroup, Input, Button, Label} from 'reactstrap';
import { NavLink } from "react-router-dom";

class Header extends Component {

    constructor(props) {
      super(props);
      this.state = {
        isNavopen: false,
        isModalopen: false
      };
      this.toggleNav = this.toggleNav.bind(this);
      this.toggleModal = this.toggleModal.bind(this);
    }

    toggleNav() {
      this.setState({
        isNavopen: !this.state.isNavopen
      });
    }

    toggleModal() {
      this.setState({
        isModalopen : !this.state.isModalopen
      });
    }

    handleLogin(event) {
      this.toggleModal();
      alert("Username : "+this.username.value+ 
      " Password :"+this.password.value+ 
      " Remember: "+this.remember.checked);
      event.preventDefault();
    }

    render() {
      return (
        <>
          <Navbar dark expand="md">
            <div className="container">
              <NavbarToggler onClick={this.toggleNav} className="mr-2"/>
              <NavbarBrand className="mr-auto" href="/">
                <img src="../../public/assets/images/logo.png" alt="Ristorante Con Fusion" height="30" width="41" />
              </NavbarBrand>
              <Collapse isOpen={this.state.isNavopen} navbar>
                  <Nav navbar>
                    <NavItem>
                      <NavLink className="nav-link" to="/home">
                        <span className="fa fa-home fa-lg"></span> Home
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className="nav-link" to="/aboutus">
                        <span className="fa fa-info fa-lg"></span> About Us
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className="nav-link" to="/menu">
                        <span className="fa fa-list fa-lg"></span> Menu
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className="nav-link" to="/contactus">
                        <span className="fa fa-address-card fa-lg"></span> Contact Us
                      </NavLink>
                    </NavItem>
                  </Nav>
                  <Nav className="ml-auto" navbar>
                    <NavItem>
                      <Button outline onClick={this.toggleModal}>
                        <span className="fa fa-sign-in fa-lg"> Login</span>
                      </Button>
                    </NavItem>
                  </Nav>
               </Collapse>
            </div>
          </Navbar>
          <Jumbotron>
              <div className="container">
                  <div className="row row-header">
                      <div className="col-12 col-sm-6">
                          <h1>Ristorante con Fusion</h1>
                          <p>We take inspiration from the World's best cuisines, and create a unique 
                          fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                      </div>
                  </div>
              </div>
          </Jumbotron>
          <Modal isOpen={this.state.isModalopen} toggle={this.toggleModal}>
            <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
            <ModalBody>
              <Form onSubmit={this.handleLogin}>
                <FormGroup>
                  <Label htmlFor="username">Username</Label>
                  <Input type="text" name="username" 
                  id="username" placeholder="username" 
                  innerRef={(input) => this.username = input} />
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="password">Password</Label>
                  <Input type="password" name="password" 
                  id="password" placeholder="password"
                  innerRef={(input) => this.password = input} />
                </FormGroup>
                <FormGroup check>
                  <Label check>
                    <Input type="checkbox" name="remember" 
                      innerRef={(input) => this.username = input} />
                    Remember me
                  </Label>
                </FormGroup>
                <FormGroup>
                  <Button type="submit" color="primary" value="submit">
                    Login
                  </Button>
                </FormGroup>
              </Form>
            </ModalBody>
          </Modal>
        </>
      );
    }
}

export default Header;