import React , { Component } from 'react';
import { Button, Breadcrumb, BreadcrumbItem, 
     Label, Col, Row } from "reactstrap";
import { Control, Form, Errors, actions } from "react-redux-form";
import { Link } from "react-router-dom";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => (val) && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

class Contact extends Component {

    constructor (props) {
        super(props);
        
        this.handleSubmit = this.handleSubmit.bind(this);     
    }

    handleSubmit(values) {
        console.log("current state is: "+JSON.stringify(values));
        //alert("current state is: "+JSON.stringify(values));
        this.props.postFeedback(values);
        this.props.resetFeedbackForm();
    }

    render() {
        return(
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>Contact Us</h3>
                        <hr />
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12">
                        <h3>Location Information</h3>
                    </div>
                    <div className="col-12 col-sm-4 offset-sm-1">
                        <h5>Our Address</h5>
                        <address>
                        121, Clear Water Bay Road<br />
                        Clear Water Bay, Kowloon<br />
                        HONG KONG<br />
                        <i className="fa fa-phone"></i>: +852 1234 5678<br />
                        <i className="fa fa-fax"></i>: +852 8765 4321<br />
                        <i className="fa fa-envelope"></i>: <a href="mailto:confusion@food.net">confusion@food.net</a>
                        </address>
                    </div>
                    <div className="col-12 col-sm-6 offset-sm-1">
                        <h5>Map of our Location</h5>
                    </div>
                    <div className="col-12 col-sm-11 offset-sm-1">
                        <div className="btn-group" role="group">
                            <a role="button" className="btn btn-primary" href="tel:+85212345678"><i className="fa fa-phone"></i> Call</a>
                            <a role="button" className="btn btn-info"><i className="fa fa-skype"></i> Skype</a>
                            <a role="button" className="btn btn-success" href="mailto:confusion@food.net"><i className="fa fa-envelope-o"></i> Email</a>
                        </div>
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12"><h3>Send us your Feedback</h3></div>
                    <Form model='feedback' className="col-12 col-md-9" onSubmit={(values) => this.handleSubmit(values)}>
                        <Row className="form-group">
                            <Label md={2} htmlFor="firstname">First Name</Label>
                            <Col md={10}>
                                <Control.text model=".firstname" name="firstname" 
                                id="firstname" placeholder="First Name" 
                                className="form-control"
                                validators = {{
                                    required, minLength: minLength(3), maxLength: maxLength(15) 
                                }} />
                                <Errors 
                                    className="text-danger"
                                    model=".firstname"
                                    show="touched"
                                    messages={{
                                        required: 'Required',
                                        minLength: 'Must be >=3 characters',
                                        maxLength: 'Must be <=15 characters'
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label md={2} htmlFor="lastname">Last Name</Label>
                            <Col md={10}>
                                <Control.text model=".lastname" name="lastname" id="lastname"  
                                placeholder="Last Name" 
                                className="form-control" 
                                validators= {{
                                    required, minLength: minLength(3), maxLength: maxLength(15)
                                }}
                                />
                                <Errors 
                                    className='text-danger'
                                    show='touched'
                                    model='.lastname'
                                    messages={{
                                        required: 'Required',
                                        minLength: 'Must be >=3 characters',
                                        maxLength: 'Must be <=15 characters',
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label md={2} htmlFor="telnum">Tel.</Label>
                            <Col md={10}>
                                <Control.text model=".telnum" name="telnum" id="telnum" 
                                placeholder="Telephone number" 
                                className="form-control" 
                                validators= {{
                                    required, minLength: minLength(10), maxLength: maxLength(12), isNumber
                                }}
                                />
                                <Errors 
                                    className="text-danger"
                                    show="touched"
                                    model=".telnum"
                                    messages= {{
                                        required: 'Required',
                                        minLength: 'Should be >= 10 characters',
                                        maxLength: 'should be <=12 characters',
                                        isNumber: 'Must be a number'
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label md={2} htmlFor="email">Email</Label>
                            <Col md={10}>
                                <Control.text model=".email" name="email" id="email" 
                                placeholder="Email" 
                                className="form-control" 
                                validators= {{
                                    required, validEmail
                                }}
                                />
                                <Errors 
                                    show="touched"
                                    className="text-danger"
                                    model=".email"
                                    messages= {{
                                        required: 'Required',
                                        validEmail: 'Invalid emailID'
                                    }}
                                />
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Col md={{size: 6, offset: 2}}>
                                <div className="form-check">
                                    <Label check>
                                        <Control.checkbox model=".agree" 
                                        name="agree" className="form-check-input" /> {' '}
                                        <strong>May we contact you ?</strong>
                                    </Label>
                                </div>
                            </Col>
                            <Col md={{size: 3, offset: 1}}>
                                <Control.select model=".contactType" 
                                name="contactType" className="form-control" >
                                    <option>Tel.</option>
                                    <option>Email</option>
                                </Control.select>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label md={2} htmlFor="message">Feedback</Label>
                            <Col md={10}>
                                <Control.textarea model=".message" 
                                name="message" id="message" rows="12"
                                className="form-control" />
                            </Col>
                        </Row>
                        <Row className="form-group offset-4 offset-md-2" >
                            <Button type="submit" color="primary" className="mr-2">
                                Submit Form
                            </Button>
                        </Row>
                    </Form>
                </div>
            </div>
        );
    }
}

export default Contact;